import { ChangeEventHandler, FC, useEffect, useMemo, useState } from 'react';
import * as ST from './styled';
import { AppLoader, PageHeader } from '@src/components';
import { IDialog, IDialogListItem, ISocketMessage } from '@src/api/Dialogs/models';
import { toastError } from '@src/utils/toast';
import { DialogsApi } from '@src/api/Dialogs/DialogsApi';
import { useDebounce, useWindowWidth } from '@src/hooks';
import { DialogItem } from '@pages/Dialogs/DialogItem';
import { ActiveDialogType, useDialogsContext } from '@providers/DialogsProvider/DialogsContext';
import { ActiveDialog } from '@pages/Dialogs/ActiveDialog';
import { useAuth } from '@providers/AuthProvider';
import { SocketApi } from '@src/api/SocketApi/SocketApi';
import { MessageEvents } from '@src/api/SocketApi/models';

const Posts: FC = () => {
  const { user } = useAuth();
  const { newDialog, clearNewDialog, activeDialog, setActiveDialog } = useDialogsContext();
  const [dialogs, setDialogs] = useState<IDialogListItem[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [search, setSearch] = useState('');
  const debouncedSearch = useDebounce(search, 400);
  const windowWidth = useWindowWidth();

  useEffect(() => {
    if (newDialog) {
      const activeUser = newDialog.users.find(it => it.id !== user?.id);

      if (activeUser) {
        setActiveDialog({
          id: newDialog.id,
          user: activeUser,
        });
      }

      clearNewDialog();
    }
  }, [newDialog]);

  const handleChangeSearch: ChangeEventHandler<HTMLInputElement> = (e) => {
    setSearch(e.target.value);
  }

  const getDialogs = async () => {
    try {
      setIsLoading(true);
      const res = await DialogsApi.fetchDialogs();

      setDialogs(res.data);
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  useEffect(() => {
    getDialogs()
  }, [debouncedSearch]);

  const handleBack = () => {
    setActiveDialog(undefined);
    getDialogs();
  }

  const showDialogs = useMemo(() => {
    return windowWidth > 1010;
  }, [windowWidth]);

  const handleClickDialog = (dialog: IDialogListItem) => {
    setActiveDialog(dialog);
    setDialogs(prev => prev.map(it => {
      if (it.id === dialog.id) {
        it.unreadableMessages = 0;
      }
      return { ...it };
    }))
  }

  useEffect(() => {
    const id = SocketApi.addListenerOfMessage((message) => {
      const parsedMessage = JSON.parse(message) as ISocketMessage;

      if (parsedMessage.event === MessageEvents.MESSAGE) {
        if (!dialogs.find(it => it.id === parsedMessage.dialogId)) {
          setDialogs(prev => [{ id: parsedMessage.dialogId, user: parsedMessage.user, unreadableMessages: 1, lastMessage: parsedMessage }, ...prev])
        } else {
          setDialogs(prev => prev.map(it => {
            if (it.id === parsedMessage.dialogId && user?.id !== parsedMessage.user.id) {
              it.unreadableMessages += 1;
              it.lastMessage = parsedMessage;
            }

            if (it.id === parsedMessage.dialogId && user?.id === parsedMessage.user.id) {
              it.lastMessage = { ...parsedMessage, read: true };
            }
            return { ...it };
          }).sort((a, b) => Date.parse(b.lastMessage.updatedAt) - Date.parse(a.lastMessage.updatedAt)))
        }
      }
    });

    return () => {
      SocketApi.removeListenerOfMessage(id);
    }
  }, []);

  const renderedDialogs = useMemo(() => {
    if (isLoading) {
      return (
        <AppLoader />
      )
    }

    if (!dialogs.length) {
      return (
        <ST.EmptyText>
          Диалогов нет
        </ST.EmptyText>
      );
    }

    if (activeDialog) {
      return (
        <ST.Content>
          {showDialogs && (
            <ST.ScrollableContent hasActive>
              <ST.DialogsContainer hasActive>
                {dialogs.map(it => <DialogItem key={it.id} dialog={it} onClick={() => handleClickDialog(it)} />)}
              </ST.DialogsContainer>
            </ST.ScrollableContent>
          )}
          <ActiveDialog dialogId={activeDialog.id} user={activeDialog.user} onBack={showDialogs ? undefined : handleBack} />
        </ST.Content>
      );
    }

    return (
      <ST.Content>
        <ST.DialogsContainer>
          {dialogs.map(it => <DialogItem key={it.id} dialog={it} onClick={() => handleClickDialog(it)} />)}
        </ST.DialogsContainer>
      </ST.Content>
    )
  }, [dialogs, isLoading, activeDialog, showDialogs]);

  return (
    <ST.Wrapper>
      <PageHeader title="Диалоги" search={search} onChangeSearch={handleChangeSearch} />
      {renderedDialogs}
    </ST.Wrapper>
  )
}

export default Posts;
