import { FC } from 'react';
import { IDialogListItem } from '@src/api/Dialogs/models';
import * as ST from './styled';
import { Avatar, AvatarSize, TextShorter } from '@src/components';
import { getTimeMessage } from '@src/utils/date';

interface IProps {
  dialog: IDialogListItem;
  onClick: () => void;
}

export const DialogItem: FC<IProps> = ({ dialog, onClick }) => {
  return (
    <ST.Wrapper onClick={onClick}>
      <Avatar
        userId={dialog.user.id}
        firstName={dialog.user.firstName}
        lastName={dialog.user.lastName}
        src={dialog.user.avatar}
        size={AvatarSize.XXXL}
      />
      <ST.Content>
        <ST.Header>
          <ST.Title tooltip>
            {dialog.user.lastName} {dialog.user.firstName} ({dialog.user.email})
          </ST.Title>

          <ST.Date>
            {getTimeMessage(dialog.lastMessage.updatedAt)}
          </ST.Date>
        </ST.Header>
        <ST.Header>
          <ST.Text>
            {dialog.lastMessage.text}
          </ST.Text>
          {Boolean(dialog.unreadableMessages) && (
            <ST.NewMessages>
              {dialog.unreadableMessages}
            </ST.NewMessages>
          )}
        </ST.Header>
      </ST.Content>
    </ST.Wrapper>
  );
}
