import styled from 'styled-components';
import { COLORS } from '@src/utils/colors';
import { TextShorter } from '@src/components';

export const Wrapper = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  gap: 12px;
  cursor: pointer;
  transition: background-color .3s;
  padding: 10px;
  overflow: hidden;
  
  &:hover {
    background-color: ${COLORS.blue20};
  }
`;

export const Header = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  width: 100%;
`;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  gap: 5px;
`;

export const Text = styled(TextShorter)`
  
`;

export const NewMessages = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  min-width: 20px;
  height: 20px;
  min-height: 20px;
  background-color: ${COLORS.blue60};
  color: ${COLORS.white};
  border-radius: 10px;
`;

export const Title = styled(TextShorter)`
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

export const Date = styled.div`
  color: ${COLORS.gray90};
`;
